@use '../base/mixins' as *;
@use '../components';

.dark-theme {
  color-scheme: dark;

  // ------ Skeleton mixin, dark scheme ------ //
  $dark-skeleton-color: #343538;
  .programs-list-wrapper-placeholder {
    .program-list-skeleton {
      @include skeleton-gradient($dark-skeleton-color);
    }
  }

  .favorites-field-skeleton {
    @include skeleton-gradient($dark-skeleton-color);
  }

  .video-skeleton-wrapper {
    .video-skeleton {
      @include skeleton-gradient($dark-skeleton-color);
    }
  }

  .basic-programs-table-placeholder {
    .animate-skeleton {
      @include skeleton-gradient($dark-skeleton-color);
    }
  }

  .programs-table-skeleton {
    .row-skeleton {
      div {
        @include skeleton-gradient($dark-skeleton-color);
      }
    }
  }

  .upload-image-placeholder {
    .upload-image-skeleton {
      @include skeleton-gradient($dark-skeleton-color);
    }
  }

  .exercise-card-skeleton {
    @include skeleton-gradient($dark-skeleton-color);
  }

  .background-wrapper-placeholder {
    .main-wrap-placeholder {
      @include skeleton-gradient($dark-skeleton-color);
    }
  }
  // ------ ------ ------ ------ //

  $dark-theme: #121317;
  .fetchingDataSpinner {
    @include adjust-color($dark-theme);
  }

  // ---- Thumbnail ---- //
  $thumbnail-hover-filter: brightness(1.1);
  $thumbnail-active-filter: brightness(1.2);

  .circle-thumbnail {
    &:hover .thumbnail-img {
      filter: $thumbnail-hover-filter;
    }

    &:active .thumbnail-img {
      filter: $thumbnail-active-filter;
    }
  }
}
